body {
  margin: 0;
  padding: 0;
  background: #222;
}
button {
  width: 100%;
}
.calculatorContainer {
  width: 500px;
  margin: 100px auto;
  box-shadow: -1px 1px 5px 13px rgba(0, 0, 0, 0.23);
}
.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 0px;
  grid-template-areas:
    ". . . ."
    ". . . ."
    ". . . ."
    ". . . ."
    ". . . .";
}
.calculatorButton {
  padding: 20px;
  font-size: 30px;
  border: solid 1px #fff;
  background: #000;
  color: #fff;
  cursor: pointer;
}
.calculatorButton:hover {
  background: #222;
}
.calculatorScreen {
  display: flex;
  flex-direction: column;
  background: #000;
  color: #fff;
  text-align: right;
}
.calculatorCurrentValue {
  font-size: 48px;
  font-weight: bold;
}
